
import './App.css';
import React, { Component } from 'react';


class App extends Component{
  state ={
      isLoading:true,
  };
  componentDidMount(){
    setTimeout(()=>{
      this.setState({isLoading:false});
    },6000);
  }
  
  render(){  
    const { isLoading } = this.state;

    return <div>{isLoading ? 'Lodaing...'  : 'We are ready'} </div>;
  }  
}

export default App;
